.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



/* my Css */
*{
  padding: 0px;
  margin: 0px;
  font-family: 'Poppins', sans-serif;
}
.list-sec{
  border: 1px solid rgb(199, 197, 197);
  margin-top: 20px;
  padding: 10px 10px !important;
  max-width: 1500px;
  margin: 50px auto;
  border-radius: 50px;
}
.list-div{
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 15px;
  text-align: center;
  padding: 25px 30px;
}
.list-div>div{
 flex-basis: 24%;
}
.list-div button{
  background-color: red;
  color: white;
  border-radius: 20px;
  padding: 8px 10px;
  border: none;
}
.description-div{
  padding: 30px 30px;
  width: 80%;
  margin: 0px auto 0px;
  border: 1px solid rgb(199, 197, 197);
  box-shadow: 0px 0px 5px 10px rgb(245, 243, 243);
  border-radius: 30px;
  text-align: left;

}
.description-div>div>div{
  margin-top: 20px;
  flex-basis: 45%;
}
.description-div>div{
  display: flex;
  flex-wrap: wrap;
}
.myPrev{
  text-decoration: none !important;
}